import React, {Component} from 'react'
import { Grid } from '@material-ui/core'
import { makeStyles } from '@material-ui/core';
//import Highcharts from 'highcharts';
//import HighchartsReact from 'highcharts-react-official';

import Detail from '../../components/Detail'
import DetailMaterial from '../../components/DetailMaterial'
//import MapBase from '../../components/Map/MapBase'

import GridPage from '../GridPage'

function createData(id, name, code, type, status) {
  return { id, name, code, type, status };
}

const rows = [
  createData('1', 'Sawit Kebun SI 01/001', 'Palmsi01001-00001', 'commodity', 'productive'),
  createData('2', 'Sawit Kebun SI 01/002', 'Palmsi01001-00002','commodity','productive'),
];

const headCells = [
  { id: 'id', numeric: false, disablePadding: true, label: 'Sequence' },
  { id: 'name', numeric: false, disablePadding: true, label: 'Commodity' },
  { id: 'code', numeric: false, disablePadding: true, label: 'Code' },
  { id: 'type', numeric: false, disablePadding: true, label: 'Type' },
  { id: 'status', numeric: false, disablePadding: true, label: 'Status' },
];

const activityrows = [
  createData('1', '2 Oct 2020', 'Penjarangan', 'Selesai'),
  createData('2', '3 Oct 2020', 'Pembersihan Gulma', 'Selesai'),
  createData('3', '4 Oct 2020', 'Pemetikan Sawit', 'Selesai'),
  createData('4', '5 Oct 2020', 'Pemupukan', 'Selesai'),
  createData('5', '5 Oct 2020', 'Perlindungan HaPe', 'Selesai'),
  createData('6', '8 Oct 2020', 'Penyiraman', 'Batal'),
  createData('7', '11 Oct 2020', 'Pemetikan Sawit', 'Selesai'),
  createData('8', '18 Oct 2020', 'Pemetikan Sawit', 'Rencana'),
];

const activityheadCells = [
  { id: 'id', numeric: false, disablePadding: true, label: 'Sequence' },
  { id: 'date', numeric: false, disablePadding: true, label: 'Date' },
  { id: 'activity', numeric: false, disablePadding: true, label: 'Activity' },
  { id: 'status', numeric: false, disablePadding: true, label: 'Status' },
];

const datatheme='Tree Management';
const objectname='Sawit Kebun SI 01/001';

const useStyles = makeStyles({
    appHeader: {
      paddingLeft: '10px',
      paddingRight: '10px',
      width: '100%',
      paddingTop: '5px',
    },
    appMaster: {
      paddingLeft: '10px',
      paddingRight: '10px',
      width: '100%',
      maxHeight: '500px',
    },
    appDetail: {
      paddingLeft: '10px',
      width: '100%',
      verticalAlign: 'top',
    }
})

function TreeManagement() {
  const classes = useStyles()
  const materials = [
    {
      date: '5 Oct 2020',
      activity: "Pemupukan",
      material: "Pupuk Bokasi",
      quantity: "1",
      unit: "kg",
    },
    {
      date: '5 Oct 2020',
      activity: "Pemupukan",
      material: "Pupuk NPK",
      quantity: "0.1",
      unit: "kg",
    },
  ]
  const tools = [
    {
      date: 'none',
      activity: "none",
      material: "none",
      quantity: "none",
      unit: "none",
    },
  ]
  const workers = [
    {
      date: '5 Oct 2020',
      activity: "Pemupukan",
      material: "Buruh Harian",
      quantity: "1",
      unit: "hari",
    },
  ]

  const detailActivity = [
    {
      objcetcode: 'Palmsi01001-00001',
      id: "1",
      group: "Tree Management",
      title: objectname,
      Commodity: 'Sawit',
      Variety: "dxpsimalungun",
      Planted: '29 Jul 2008',
      Status: "Production",
      Total: '120 tbs',
      Activity: 'Pemupukan',
      Date: '5 Oct 2020',
      COntractor: 'Pegawai Perusahaan'
    },
  ]
  const options = {
    chart: {
      type: 'spline'
    },
    title: {
      text: 'My chart'
    },
    series: [
      {
        data: [1, 2, 1, 4, 3, 6]
      }
    ]
  };
  return (
    <>
      <Grid container className={classes.appDetail}>
      <Grid className={classes.appHeader} sm={6}>
          <GridPage rows={rows} headCells={headCells} datatheme={datatheme} />
          </Grid>
          <Grid className={classes.appHeader} sm={6}>
          <GridPage rows={activityrows} headCells={activityheadCells} datatheme={datatheme + "(" + objectname + ") Activities"} />
          </Grid>
      </Grid>
      <Grid container className={classes.appDetail}>
          <Grid className={classes.appHeader} item sm={6}>
          <Detail itemContent={detailActivity} datatheme={datatheme + " Activity Detail"}/>
          </Grid>
          <Grid className={classes.appHeader} item sm={6}>
          <DetailMaterial materials={materials} tools={tools} workers={workers}/>
          </Grid>
      </Grid>
    </>
  )
}

export default TreeManagement;