//import { makeStyles } from '@material-ui/core'
import React from 'react'

const TableHeader = (props) => {
    const tablegroup = props.tableGroup
    
    switch(tablegroup[0].groupname) {

          case "Materials":   
          return (
            <thead>
              <tr>
                <th>Date</th>
                <th>Actvity</th>
                <th>Material</th>
                <th>Quantity</th>
                <th>Unit</th>
              </tr>
            </thead>
          )

          case "Tools":   
          return (
              <thead>
                <tr>
                  <th>Date</th>
                  <th>Actvity</th>
                  <th>Tool</th>
                  <th>Quantity</th>
                  <th>Unit</th>
                </tr>
              </thead>
            )
            case "Workers":   
            return (
                <thead>
                  <tr>
                    <th>Date</th>
                    <th>Actvity</th>
                    <th>Skill</th>
                    <th>Volume</th>
                    <th>Unit</th>
                  </tr>
                </thead>
              )
    
        default:
            return <h1>No project match</h1>
      }
}


//const TableBody = () => {
const TableBody = (props) => {
    const tablegroup = props.tableGroup
    switch(tablegroup[0].groupname) {

        case "Materials":
            const rowmat = props.characterData.map((row, index) => {
                return (
                  <tr key={index}>
                    <td>{row.date}</td>
                    <td>{row.activity}</td>
                    <td>{row.material}</td>
                    <td>{row.quantity}</td>
                    <td>{row.unit}</td>
                  </tr>
                )
              })
              return <tbody>{rowmat}</tbody>	
        case "Tools":   
            const rowtol = props.characterData.map((row, index) => {
                return (
                  <tr key={index}>
                    <td>{row.date}</td>
                    <td>{row.activity}</td>
                    <td>{row.material}</td>
                    <td>{row.quantity}</td>
                    <td>{row.unit}</td>
                  </tr>
                )
              })
              return <tbody>{rowtol}</tbody>	
        case "Workers":   
            const rowor = props.characterData.map((row, index) => {
                return (
                  <tr key={index}>
                    <td>{row.date}</td>
                    <td>{row.activity}</td>
                    <td>{row.material}</td>
                    <td>{row.quantity}</td>
                    <td>{row.unit}</td>
                  </tr>
                )
              })
              return <tbody>{rowor}</tbody>	
    
        default:      
            return <h1>No project match</h1>
      }


}

const Table = (props) => {
    const {characterData, tableGroup} = props
  
    return (
      <table>
        <TableHeader tableGroup={tableGroup} />
        <TableBody characterData={characterData} tableGroup={tableGroup} />
      </table>
    )
  }
  export default Table