import React from 'react'
import { AppBar, Tab, Tabs, Box, makeStyles } from '@material-ui/core'
import Table from './Table'
//import SettingsIcon from '@material-ui/icons/Settings';
import EcoIcon from '@material-ui/icons/Eco';
import BuildIcon from '@material-ui/icons/Build';
import PeopleIcon from '@material-ui/icons/People';

//https://medium.com/javascript-in-plain-english/material-ui-tabs-ee580daa62de

const useStyles=makeStyles({
  appDetailContent: {
    paddingRight: '10px',
    width: '100%',
    maxHeight: '270px',
    overflow:'auto',
    paddingTop: '5px',
    paddingbottom: '5px',
  },
  appDetailBar: {
    paddingRight: '10px',
    width: '100%',
    maxHeight: '70px',
  },
})

function TabPanel(props) {
    const { children, value, index, ...other } = props;  return (
      <div {...other}>
        {value === index && <Box p={3}>{children}</Box>}
      </div>
    );
}

//export default function Detail() {
const Detail = (props) => {
  const classes = useStyles()
  const characters = props.itemContent
  const datatheme  = props.datatheme
  const groupmat = [
    {
      groupname: 'Materials',
    },
  ]
  const grouptol = [
    {
      groupname: 'Tools',
    },
  ]
  const groupwor = [
    {
      groupname: 'Workers',
    },
  ]

  const [value, setValue] = React.useState(0); 
  const handleChange = (event, newValue) => {
      setValue(newValue);
    };

  const DivContent = (props) => {
    const charSet = props.characterData[0];
    const contents = Object.keys(charSet).map((key, i) => {
        return (
          <tr key={i}>
                <td>{key}</td>
                <td>{charSet[key]}</td>
          </tr>
        )
      })
    return <tbody>{contents}</tbody>	

  }

  return (
    <>
      <AppBar position="static" className={classes.appDetailBar}>
          <Tabs variant="fullWidth">
          <Tab icon={<EcoIcon />} label={datatheme} />
          </Tabs>
      </AppBar>

      <div className={classes.appDetailContent}>
        <DivContent
            characterData={characters}
            tableGroup={groupmat}
        />
      </div>
    </>
  );
}

export default Detail