import React, { Component } from 'react';
import { HashRouter, Route, Link } from "react-router-dom";

import PrivateRoute from './Utils/PrivateRoute';
import PublicRoute from './Utils/PublicRoute';

import Home from "./components/MainPage";
import Header from "./components/HeaderNavbar";

import Login from "./Login";

import QRReader from "./components/QRRead";
import AssetDistribution from "./components/AssetDistribution/AssetDistribution";
import InfrastructureTransaction from "./components/DataTransaction/InfrastructureTransaction";
import LandstructureTransaction from "./components/DataTransaction/LandstructureTransaction";
import TreeManagementTransaction from "./components/DataTransaction/TreeManagementTransaction";
import FleetTransaction from "./components/DataTransaction/FleetTransaction";

class App extends Component {
  render() {
    return (
      <HashRouter basename="/">
        <div>
          <Header />          
        </div>
        <div>
          <Route exact path="/" component={Home} />
          <PublicRoute path="/Login" component={Login} />
          <PrivateRoute path="/QRReader" component={QRReader} />
          <PrivateRoute path="/AssetDistribution" component={AssetDistribution} />
          <PrivateRoute path="/InfrastructureTransaction" component={InfrastructureTransaction} />
          <PrivateRoute path="/LandstructureTransaction" component={LandstructureTransaction} />
          <PrivateRoute path="/TreeManagementTransaction" component={TreeManagementTransaction} />
          <PrivateRoute path="/FleetTransaction" component={FleetTransaction} />
        </div>
      </HashRouter>
    );
  }
}

export default App;