import React from "react";
import { Link } from "react-router-dom";

import {
  AppBar,
  Toolbar,
  Container,
  IconButton,
  List,
  ListItem,
  ListItemText,
  Hidden
} from "@material-ui/core";
import { makeStyles } from "@material-ui/core";
import { Home } from "@material-ui/icons";
import SideDrawer from "../Utils/SideDrawer";

const useStyles = makeStyles({
  navbarDisplayFlex: {
    display: `flex`,
    justifyContent: `space-between`
  },
  navDisplayFlex: {
    display: `flex`,
    justifyContent: `space-between`
  },
  linkText: {
    textDecoration: `none`,
    textTransform: `uppercase`,
    color: `white`
  }
});

const navLinks = [
  { title: `QR ----     |`, path: `/QRReader` },
  { title: `Asset ----     |`, path: `/AssetDistribution` },
  { title: `Infrastructure ----    | `, path: `/InfrastructureTransaction` },
  { title: `Landstructure ----     |`, path: `/LandstructureTransaction` },
  { title: `Tree Management ----    | `, path: `/TreeManagementTransaction` },
  { title: `Fleet`, path: `/LandstructureTransaction` },
];


function Header(props) {
//const Header = () => {
  const classes = useStyles();

  return (
    <AppBar position="static">
      <Toolbar>
        <Container maxWidth="md" className={classes.navbarDisplayFlex}>
          <IconButton 
            href={"/"}
            edge="start" 
            color="inherit" 
            aria-label="home">
            <Home fontSize="large" />
          </IconButton>
          <Hidden smDown>
            <List
              component="nav"
              aria-labelledby="main navigation"
              className={classes.navDisplayFlex}
            >
              {navLinks.map(({ title, path }) => (
                  <div>
                    <Link to={path} className={classes.linkText}>{title}</Link>
                  </div>

                //<a href={path} key={title} className={classes.linkText}>
                //  <ListItem button>
                //    <ListItemText primary={title} />
                //  </ListItem>
                //</a>
              ))}
            </List>
          </Hidden>
          <Hidden mdUp>
            <SideDrawer navLinks={navLinks} />
          </Hidden>
        </Container>
      </Toolbar>
    </AppBar>
  );
};

export default Header;