import React from 'react'
import { AppBar, Tab, Tabs, Box, makeStyles } from '@material-ui/core'
import Table from './Table'
//import SettingsIcon from '@material-ui/icons/Settings';
import EcoIcon from '@material-ui/icons/Eco';
import BuildIcon from '@material-ui/icons/Build';
import PeopleIcon from '@material-ui/icons/People';

//https://medium.com/javascript-in-plain-english/material-ui-tabs-ee580daa62de

const useStyles=makeStyles({
  appDetailContent: {
    paddingRight: '10px',
    width: '100%',
    maxHeight: '270px',
    overflow:'auto',
    paddingTop: '5px',
    paddingbottom: '5px',
  },
  appDetailBar: {
    paddingRight: '10px',
    width: '100%',
    maxHeight: '70px',
  },
})

function TabPanel(props) {
    const { children, value, index, ...other } = props;  return (
      <div {...other}>
        {value === index && <Box p={3}>{children}</Box>}
      </div>
    );
}

//export default function Detail() {
const DetailMaterial = (props) => {
  const classes = useStyles()
  const materials = props.materials
  const tools = props.tools
  const workers = props.workers
  const groupmat = [
    {
      groupname: 'Materials',
    },
  ]
  const grouptol = [
    {
      groupname: 'Tools',
    },
  ]
  const groupwor = [
    {
      groupname: 'Workers',
    },
  ]

  const [value, setValue] = React.useState(0);
  
  const handleChange = (event, newValue) => {
      setValue(newValue);
    };
  return (
    <>
      <AppBar position="static" className={classes.appDetailBar}>
        <Tabs value={value} onChange={handleChange} variant="fullWidth">
          <Tab icon={<EcoIcon />} label="Materials" />
          <Tab icon={<BuildIcon />} label="Tools" />
          <Tab icon={<PeopleIcon />} label="Workers" />
        </Tabs>
      </AppBar>
      <div className={classes.appDetailContent}>
        <TabPanel value={value} index={0}>
        <Table
            characterData={materials}
            tableGroup={groupmat}
        />
        </TabPanel>
        <TabPanel value={value} index={1}>
        <Table
            characterData={tools}
            tableGroup={grouptol}
        />
        </TabPanel>
        <TabPanel value={value} index={2}>
        <Table
            characterData={workers}
            tableGroup={groupwor}
        />
        </TabPanel>
      </div>
    </>
  );
}

export default DetailMaterial