import React, { Component } from 'react'
import {
  Circle,
  FeatureGroup,
  LayerGroup,
  LayersControl,
  Map,
  Marker,
  Polygon,
  Polyline,  
  Popup,
  Rectangle,
  TileLayer,
} from 'react-leaflet'
//import ReactDOM from 'react-dom'
import '../../index.css'

const { BaseLayer, Overlay } = LayersControl
const center = [0.49900, 101]
const rectangle = [
  [0.49900, 100.8],
  [0.49900, 101.2],
]

export default class MapBase extends Component<{}> {
  render() {
    return (
      <Map center={center} zoom={17}>
        <LayersControl position="topright">
          <BaseLayer name="OpenStreetMap">
            <TileLayer
              attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
              url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
            />
          </BaseLayer>
          <BaseLayer checked name="Satelite Imagery">
            <TileLayer
              attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
              url="https://server.arcgisonline.com/ArcGIS/rest/services/World_Imagery/MapServer/tile/{z}/{y}/{x}"
            />
          </BaseLayer>
          <Overlay checked name="Asset Marker">
          <LayerGroup>
            <Marker position={[0.50003, 100.99896]}>
              <Popup>
                Pos Jaga
              </Popup>
            </Marker>
            <Marker position={[0.49926, 100.99922]}>
              <Popup>
                Jembatan Timbang
              </Popup>
            </Marker>
            <Marker position={[0.49907, 100.99971]}>
              <Popup>
                Pabrik Kelapa Sawit
              </Popup>
            </Marker>
            <Marker position={[0.49848, 101.00111]}>
              <Popup>
                Jembatan Titian
              </Popup>
            </Marker>
            <Marker position={[0.49910, 100.99996]}>
              <Popup>
                Gudang
              </Popup>
            </Marker>
            <Marker position={[0.49939, 100.99888]}>
              <Popup>
                Kantor
              </Popup>
            </Marker>
            <Marker position={[0.49903, 100.99883]}>
              <Popup>
              Pemukiman
              </Popup>
            </Marker>
            <Marker position={[0.49836, 101.00091]}>
              <Popup>
              Penampungan Air
              </Popup>
            </Marker>
            <Marker position={[0.49983, 101.00003]}>
              <Popup>
              Sawit Kebun SI 01/001
              </Popup>
            </Marker>
          </LayerGroup>
          </Overlay>
          <Overlay checked name="Asset Polyline">
            <LayerGroup>
              <Polyline color="blue" positions={
                [
                  [0.50045, 100.99811],
                  [0.50040, 100.99828],
                  [0.50031, 100.99848],
                  [0.50023, 100.99893],
                  [0.50019, 100.99926],
                  [0.50030, 100.99966],
                  [0.50044, 100.99994],
                  [0.50063, 101.00015],
                ]
              }>
              <Popup>
                Jalan Utama
              </Popup>                
              </Polyline>
              <Polyline color="blue" positions={
                [
                  [0.50045, 100.99811],
                  [0.50040, 100.99828],
                  [0.50031, 100.99848],
                  [0.50023, 100.99893],
                  [0.50019, 100.99926],
                  [0.50030, 100.99966],
                  [0.50044, 100.99994],
                  [0.50063, 101.00015],
                ]
              }>
              <Popup>
                Jalan Utama
              </Popup>                
              </Polyline>  
              <Polyline color="blue" positions={
                [
                  [0.50023, 100.99893],
                  [0.49920, 100.99902],
                  [0.49804, 100.99913],
                ]
              }>
              <Popup>
              Jalan blok SI 01/001
              </Popup>                
              </Polyline>
            </LayerGroup>
          </Overlay>
          <Overlay checked name="Asset Polygon">
            <Polygon color="purple" positions={
            [
              [0.49926, 100.99904],
              [0.50023, 100.99895],
              [0.50017, 100.99926],
              [0.50028, 100.99966],
              [0.50042, 100.99994], 
              [0.50061, 101.00015],
              [0.49926, 101.00015],
              [0.49926, 100.99915],
            ]
            }>
            <Popup>
            Block 01/001
            </Popup>                
            </Polygon>
          </Overlay>
        </LayersControl>
      </Map>
    )
  }
}
